// Stylesheets
import '../index.css'

function Banner(props) {
  return (
    <div className="banner">
      <img
        className="banner-image"
        alt="Gif Pokémon"
        src="https://media.giphy.com/media/mMvxVzlCFabUWnuyBA/source.gif"
      />
      <h1 className="banner-title">Pokédex</h1>
    </div>
  )
}

export default Banner
